import React from "react";

export const WeaPopoverHrm = props => props.children;
export const WeaErrorPage = () => null;
export const WeaDialog = () => null;
export const WeaDropdown = () => null;
export const WeaAlertPage = () => null;
export const WeaRightMenu = props => (
  <div style={{ height: '100%', position: 'relative' }}>
    {props.children || null}
  </div>
);

export const WeaBrowser = props => {
  const { replaceDatas, linkUrl } = props;
  if (Array.isArray(replaceDatas) && replaceDatas.length > 0) {
    if (linkUrl) {
      const style = {
        display: 'inline-block',
        cursor: 'pointer',
        color: '#00A9FF',
        margin: '1px 5px 1px 0',
        wordBreak: 'break-all',
        wordWrap: 'break-word',
      }
      return replaceDatas.map(r => (
        <a style={style}>{r.name || r.showname}</a>
      ));
    }
    return replaceDatas.map(r => r.name || r.showname).join(', ');
  }
  return null;
}
export const WeaUpload = () => null;
export const WeaSelect = () => null;
export const WeaDatePicker = () => null;
export const WeaTimePicker = () => null;
export const WeaCheckbox = () => null;
export const WeaRichText = () => null;

export default () => null;



