import isString from 'lodash/isString';
import * as caUtil from "@callApi";
const callApi = caUtil.callApi;
const localeKey = 'e9_locale';
export const getLabel = (id = '', defaultValue, type) => {
  let loc = window[localeKey] || {};
  if (type) {
    loc = loc[type];
  } else {
    loc = loc.label;
  }
  if (!loc) return defaultValue || '';
  let text = loc[id];
  if (isString(id) && id.indexOf(',') > -1) { //
    text = id.split(',').reduce((t, c, i)=>`${i==1?loc[t]:t}${loc[c]}`); 
  }
  if (isString(text) && text.indexOf('undefined') > -1 && defaultValue) {
    text = defaultValue
    // addLabelLog(id, defaultValue)
  };
  let str = text || defaultValue;
  if (!text) {
    // addLabelLog(id, defaultValue)
  }
  return formatStr(str);
};

